@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Special+Elite);
@import url(https://fonts.googleapis.com/css?family=Cormorant+Garamond);
@import url(https://fonts.googleapis.com/css?family=Finger+Paint);
@import url(https://fonts.googleapis.com/css?family=Lato);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/********/
 /* TAGS */
/********/
nav{
  flex: 1 1;
  text-align: center;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
}


  /***********/
 /* CLASSES */
/***********/
.top-bar-container{
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  background-color: #bdbcc2;
  border-bottom: 2px solid #aeaeae;
}

.footer{
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  margin-top: 75px;
  height: 150px;
}


.landing-link, .library-link{
  text-decoration: none;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  font-weight: 400px;
}

.landing-link:hover, .library-link:hover{
  color: #ffffff;
  text-decoration: underline;
}


:visited{
  color: #353535;
  text-decoration: underline;
}


#separator{
  padding-left: 15px;
  padding-right: 15px;
  color: #757575;
}


.main-title-container{
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}


.bloc-jams-title{
  flex: 3 1;
  height: 180px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  font-size: 90px;
  font-family: 'Special Elite', cursive;
  color: #353535;
}


/***********/
 /* CLASSES */
/***********/

.selling-points{
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    background-image: url("/assets/images/landing_images/wall.jpg");
    background-size: 150%;
    width: 800px;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 50px;
    border-radius: 0px 0px 40px 40px;
    height: 1465px;
    opacity: 0.9;
    box-shadow: 16px 12px 45px 1px rgba(0,0,0,0.5);
}


.hero-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    box-shadow: 16px 12px 45px 1px rgba(0,0,0,0.5);
    margin-right: auto;
    margin-left: auto;
    width: 800px;
    border-radius: 40px 40px 0px 0px;
}


.hero-title {
    color: #ffffff;
    background-image: url("/assets/images/landing_images/orange-gradient.jpg");
    text-align: center;
    font-family: 'Finger Paint', cursive;
    font-weight: 400;
    font-size: 75px;
    text-shadow: 2px 2px 8px #101010;
    height: 240px;
    width: 800px;
    margin-top: 0px;
    margin-bottom: 0px;
    display: flex;
    justify-content: center;
    align-items: center;

}


.hero-image{
    border-radius: 40px 40px 0px 0px;
}

.point-1-container, .point-2-container, .point-3-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; 
}


.point-title-1, .point-title-2, .point-title-3{
    color: #ffffff;
    text-align: center;
    font-family: 'Finger Paint', cursive;
    font-weight: 400;
    font-size: 54px;
    text-shadow: 2px 2px 8px #101010;
    height: 240px;
    width: 800px;
    margin-top: 0px;
    margin-bottom: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
}


.point-description-1, .point-description-2{

    width: 800px;
    font-family: 'Cormorant Garamond', serif;
    font-size:34px;
    font-weight: 600;
    color: #a2fdff;
    background-color: #656277e1;
    text-align: center;
    line-height: 1.5em;
    height: 210px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    text-shadow: 1px 1px 5px #000000;
}


.point-description-3{
    width: 800px;
    font-family: 'Cormorant Garamond', serif;
    font-size: 34px;
    font-weight: 600;
    color: #a2fdff;
    background-color: #656277e1;
    text-align: center;
    line-height: 1.5em;
    height: 210px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    text-shadow: 1px 1px 5px #000000;
}

/***********/
 /* CLASSES */
/***********/

.library-container{
    display: flex;
    background-image: url("/assets/images/landing_images/orange-gradient.jpg");
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin-top: 50px;
    opacity: .9;
    box-shadow: 16px 12px 45px 1px rgba(0,0,0,0.5);
}

.album-container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 500px;
    margin-top: 40px;
}


.album-info-container{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    color: white;
    font-family: 'Lato', sans-serif;
    font-size: 22px;
    font-weight: 100;
    text-decoration: none;
    line-height: 1.5em;
    height: 150px;
    margin-left: 25px;
    text-shadow: 2px 2px 1px #131313;
    margin-left: 65px;
}


a{
    text-decoration: none;
}


.album-info-container:hover{
    text-decoration: underline;
}


.album-cover{
    height: 300px;
    width: 300px;
    border-radius: 20%;
    box-shadow: 13px 11px 16px -2px rgba(0,0,0,0.73);
    border: 2px solid white;
}

  /***********/
 /*  ID's   */
/***********/
#time-control{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 240px;
    margin-top: 20px;
}


#buttons{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100px;
    margin-top: 10px;
}


#volume-control{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 190px;
    margin-top: 20px;
}


  /***********/
 /* CLASSES */
/***********/
.player-bar-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin-top: 0px;
    margin-bottom: 75px;
    color: black;
    line-height: 1em;
    font-size: 24px;
    font-weight: 500;
}



  /********/
 /* TAGS */
/********/
td, th, tr {
    width: 10rem;
    height: 3rem;
    text-align: center;
    color: white;
    text-shadow: 2px 2px 2px #101010;
    line-height: 1em;
    font-size: 26px;
    font-weight: 400;
    border-bottom: 1px dashed #ffffff;
  }


  th {
    background: lightblue;
    border-color: white;
  }


  /***********/
 /*  ID's   */
/***********/
#album-cover-art{
    height: 350px;
    width: 350px;
    border-radius: 20%;
    box-shadow: 13px 11px 16px -2px rgba(0,0,0,0.73);
    border: 2px solid white;
    margin-top: 0px;
}


#song-list{
    display: flex;
    flex-direction: column;
    width: 800px;
    justify-content: center;
    align-items: center;
    color: white;
    text-shadow: 2px 2px 2px #101010;
    line-height: 1em;
    font-size: 20px;
    margin-bottom: 30px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    margin-bottom: 50px;
}


  /***********/
 /* CLASSES */
/***********/
.album{
    display: flex;
    background-image: url("/assets/images/landing_images/orange-gradient.jpg");
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
    height: 1000px;
    width: 800px;
    opacity: .9;
    border-radius: 40px;
    box-shadow: 16px 12px 45px 1px rgba(0,0,0,0.5);
}


.album-details{
    margin-left: 35px;
}

.album-info{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    height: 480px;
    color: white;
    text-shadow: 2px 2px 8px #101010;
    line-height: 1em;
    font-size: 26px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 0px;
}


.player-cotainer{
    display: row;
    flex-direction: column;

}
